import "./App.css";
import Navbar from "./Containers/Navbar/Navbar";
import Gallery from "./Components/ImageGallery/Gallery";
import { InputContext } from "./Context/InputContext";
import { ImagesContext } from "./Context/ImagesContext";
import { useState } from "react";
import { SortContext } from "./Context/SortContext";
import { SortListContext } from "./Context/SortListContext";
import { ImageClickedContext } from "./Context/ImageClickedContext";
function App() {
  const [inputContext, setInputContext] = useState("");
  const [imagesContext, setImagesContext] = useState([]);
  const [sortContext, setSortContext] = useState("");
  const [sortList, setSortList] = useState([]);
  const [imageClicked, setImageCLicked] = useState(false);
  return (
    <>
      <InputContext.Provider value={[inputContext, setInputContext]}>
        <ImagesContext.Provider value={[imagesContext, setImagesContext]}>
          <SortContext.Provider value={[sortContext, setSortContext]}>
            <SortListContext.Provider value={[sortList, setSortList]}>
              <ImageClickedContext.Provider
                value={[imageClicked, setImageCLicked]}
              >
                <Navbar />
                <Gallery />
              </ImageClickedContext.Provider>
            </SortListContext.Provider>
          </SortContext.Provider>
        </ImagesContext.Provider>
      </InputContext.Provider>
    </>
  );
}

export default App;
