import React, { useContext, useEffect, useState } from "react";
import axios from "axios";
import "./Gallery.css";
import { InputContext } from "../../Context/InputContext";
import { ImagesContext } from "../../Context/ImagesContext";
import { SortContext } from "../../Context/SortContext";
import { ImageClickedContext } from "../../Context/ImageClickedContext";
import { SortListContext } from "../../Context/SortListContext";

//url variables
const baseUrl =
  "https://pixabay.com/api/?key=30686155-a62a548cabacbf09725d76ec9";
const restParam = "&image_type=photo&safesearch=true&pretty=true";

//Component
function Gallery() {
  const [imagesContext, setImagesContext] = useContext(ImagesContext);
  const [inputContext] = useContext(InputContext);
  const [sortContext] = useContext(SortContext);
  const [sortList, setSortList] = useContext(SortListContext);
  const [imageClicked, setImageClicked] = useContext(ImageClickedContext);

  const [pageNumber] = useState(1);
  const [imageDetail, setImageDetail] = useState([]);

  const handleClick = (i) => {
    setImageClicked(true);
    const newList = imagesContext.filter((item) => item.id === i);
    setImageDetail(newList);
  };

  const fetchImages = async () => {
    const response = await axios.get(
      `${baseUrl}&q=${inputContext}${restParam}&page=${pageNumber}`
    );
    setImagesContext(response.data.hits);
  };

  useEffect(() => {
    fetchImages();

    if (sortContext === "S") {
      setSortList(
        imagesContext.sort((obj1, obj2) =>
          obj1.imageSize < obj2.imageSize
            ? 1
            : obj1.imageSize > obj2.imageSize
            ? -1
            : 0
        )
      );
    } else if (sortContext === "V") {
      setSortList(
        imagesContext.sort((obj1, obj2) =>
          obj1.views < obj2.views ? 1 : obj1.views > obj2.views ? -1 : 0
        )
      );
    }
  }, [inputContext, sortContext]);

  return (
    <div className="gallery flex-display">
      <div className="image-div-gallery flex-display">
        {sortContext
          ? sortList.map((item) => {
              return (
                <div>
                  <img
                    className="image-div-images solid-border-styles"
                    alt="pixabay images"
                    onClick={() => handleClick(item.id)}
                    key={item.id}
                    src={item.webformatURL}
                  />
                  <p>Size: {item.imageSize}</p>
                  <p>Views: {item.views}</p>
                </div>
              );
            })
          : imagesContext.map((item) => {
              return (
                <div>
                  <img
                    className="image-div-images solid-border-styles"
                    alt="pixabay images"
                    onClick={() => handleClick(item.id)}
                    key={item.id}
                    src={item.webformatURL}
                  />
                  <p>Size: {item.imageSize}</p>
                  <p>Views: {item.views}</p>
                </div>
              );
            })}
      </div>
      {imageClicked ? (
        <div className="detailed-image-section flex-display">
          <button onClick={() => setImageClicked(false)}>Close</button>
          <img
            className="detailed-image solid-border-styles"
            src={imageDetail[0].webformatURL}
            alt=""
          />
          <div className="detailed-image-assets flex-display">
            <p>Views: {imageDetail[0].views}</p>
            <p>Downloads: {imageDetail[0].downloads}</p>
            <p>Likes: {imageDetail[0].likes}</p>
            <p>Comments: {imageDetail[0].comments}</p>
          </div>
        </div>
      ) : (
        <></>
      )}
    </div>
  );
}

export default Gallery;
