import React, { useContext, useState } from "react";
import { ImageClickedContext } from "../../Context/ImageClickedContext";
import { InputContext } from "../../Context/InputContext";
import { SortContext } from "../../Context/SortContext";
import "./SearchBar.css";
function SearchBar() {
  const [input, setInput] = useState("");
  const [inputContext, setInputContext] = useContext(InputContext);
  const [sortContext, setSortContext] = useContext(SortContext);
  const [imageClicked, setImageCLicked] = useContext(ImageClickedContext);

  const handleChange = (e) => {
    let value = e.target.value;
    setInput(value);
  };

  const handleKeyDown = (e) => {
    let modifiedValue = input.split(" ").join("+");

    if (e.key === "Enter") {
      setInputContext(modifiedValue);
      setSortContext("");
      setImageCLicked(false);
    }
  };
  return (
    <div className="searchBar">
      <input
        className="searchBar-input"
        type="text"
        onChange={handleChange}
        value={input}
        onKeyDown={handleKeyDown}
        placeholder="Search for an Image"
      />
      <span
        onClick={() => {
          setInput("");
          setInputContext("");
          setSortContext("");
          setImageCLicked(false);
        }}
        className="searchBar-clear"
        style={{ display: input ? "block" : "none" }}
      >
        Clear
      </span>
    </div>
  );
}

export default SearchBar;
